.works {
	background-color: #525252;
	padding: 80px 0 40px;
	@include r(450) {
		padding-top: 40px; }
	&__heading {
		color: #e7e0d6;
		font-family: Akrobat;
		font-size: 44px;
		text-transform: uppercase;
		margin-bottom: 30px;
		text-align: center;
		&_blue {
			color: #3eb5f1; }
		@include r(450) {
			font-size: 30px;
			margin-bottom: 20px; } }
	&__subtitle {
		color: #eeeeee;
		font-family: Akrobat;
		font-size: 25px;
		text-transform: uppercase;
		text-align: center;
		margin-bottom: 35px;
		@include r(450) {
			font-size: 16px;
			margin-bottom: 15px; }
		@include r(360) {
			font-size: 12px; } }
	&__list {
		margin-bottom: 20px; }
	&__item {
		position: relative;
		margin-bottom: 19px;
		width: 45%;
		margin: 10px 10px;
		@include r(445) {
			margin: 5px; } }

	&__image {
		max-width: 560px;
		max-height: 357px;
		z-index: 1;
		overflow: hidden;
		img {
			width: 100%; } }
	&__list {
		display: flex;
		flex-wrap: wrap;
		-webkit-justify-content: space-between;
		justify-content: space-between;
		@include r(1000) {
			-webkit-justify-content: center;
			justify-content: center; } }
	&__caption {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: #FFF;
		height: 60px;
		z-index: 3;
		line-height: 60px;
		padding: 0 40px;
		color: #3b3b3b;
		font-family: Akrobat;
		font-size: 22px;
		@include r(750) {
			font-size: 16px;
			height: auto;
			line-height: 1;
			padding: 10px 20px; }
		@include r(450) {
			font-size: 12px;
			padding: 8px 15px; } }
	&__place {
		font-variant: small-caps;
		display: inline-block;
		float: left;
		@include r(800) {
			float: none;
			text-align: center;
			width: 100%; } }
	&__area {
		text-transform: uppercase;
		font-family: Akrobat;
		display: inline-block;
		float: right;
		@include r(800) {
			display: none; } }
	&__button {
		padding: 13px 25px;
		text-decoration: none;
		border: 2px solid #6ac0f3;
		color: #ffffff;
		font-family: Akrobatbold;
		/* semi*/
		font-size: 20px;
		text-transform: uppercase;
		background-color: transparent;
		margin: 0 auto;
		display: block;
		border-radius: 22px;
		@include r(460) {
			font-size: 16px; } } }
