.small-woods {
	font-size: 0;
	padding: 80px 0;
	@include r(1400) {
		zoom: 0.7; }
	@include r(930) {
		zoom: 0.5;
		padding: 60px 0; }
	@include r(700) {
		zoom: 0.3;
		padding: 60px 0 40px; }
	.container {
		position: relative; }
	&__item {
		@include clr;
		position: relative;
		top: 200px;
		left: -50px;
		@include r(700) {
			top: 240px; } }
	&__image {
		width: 443px;
		position: relative;
		float: left;
		padding-left: 50px;
		color: #000000;
		font-family: Akrobat;
		font-size: 34px;
		text-transform: uppercase;
		z-index: 3;
		top: -200px;
		@include r(700);
		&_bottom {
			float: right; } }
	&__sale {
		position: absolute;
		width: 101px;
		height: 101px;
		border: 2px solid #7b7b7b;
		background-color: rgba(255,255,255,.5);
		text-align: center;
		display: block;
		content: '';
		color: #d24a43;
		font-family: Akrobatbold;
		/*semi*/
		font-size: 48px;
		font-weight: bold;
		text-transform: uppercase;
		// Text style for "-50%"
		letter-spacing: 2.28px;
		line-height: 105px;
		top: 0px;
		left: 0;
		@include r(700) {
			display: none; } } }
.small-woods__image_bottom .small-woods__sale {
	top: auto;
	left: auto;
	right: -101px;
	bottom: -55px; }
.stock-sw {
	text-align: center;
	background-color: #808080;
	padding: 46px 0 64px;
	width: 379px;
	margin: 0 auto;
	position: relative;
	left: 50px;
	z-index: 1;
	&__title {
		color: #ffffff;
		font-family: Akrobatbold;
		/*semi*/
		font-size: 42px;
		font-weight: bold;
		text-transform: uppercase;
		letter-spacing: 2.38px;
		&:after {
			display: block;
			content: '';
			width: 190px;
			height: 2px;
			background-color: #3eb5f1;
			margin: 16px auto 20px; } }
	&__only {
		color: #ffffff;
		font-family: Akrobat;
		font-size: 21px;
		text-transform: uppercase;
		letter-spacing: 1.2px;
		margin-bottom: 43px; }
	&__old-price {
		color: #ffffff;
		font-family: Akrobat;
		font-size: 26px;
		font-weight: 400;
		text-decoration: line-through;
		text-transform: uppercase;
		letter-spacing: 1.71px;
		margin-bottom: 20px; }
	&__price {
		text-shadow: 3px 4px 5px rgba(0, 0, 0, 0.15);
		color: #fd7979;
		font-family: Akrobat;
		font-size: 37px;
		text-transform: uppercase;
		letter-spacing: 2.38px;
		margin-bottom: 39px; }
	&__sale {
		color: #ffffff;
		font-family: Akrobat;
		font-size: 20px;
		font-weight: 400;
		text-transform: uppercase;
		letter-spacing: 0.86px;
		margin-bottom: 40px; }
	&__button {
		background-color: transparent;
		width: 170px;
		border: 2px solid #ffd357;
		border-radius: 20px;
		font-family: Akrobatbold;
		font-size: 19px;
		text-transform: uppercase;
		letter-spacing: 1.17px;
		padding: 14px 0; } }
.small-woods__text {
	padding-left: 80px;
	@include r(700) {
		padding: 0;
		text-align: center;
		margin-bottom: 10px; } }
