body, html {
	margin: 0px;
	padding: 0; }
@include font('Akrobat', 'Akrobat');
@include font('Akrobatbold', 'Akrobatbold');
@include font('Akrobatlight', 'Akrobatlight');
@include font('Panton', 'Panton');
@include font('PantonBold', 'PantonBold');
@include font('PantonLight', 'PantonLight');
/*+font('KabzarKS', 'KabzarKS')*/
@include font('LucidaSans', 'LucidaSansUnicode');

@font-face {
    font-family: 'KobzarKS';
    src: url('../fonts/KobzarKS.woff2') format('woff2'), url('../fonts/KobzarKS.woff') format('woff');
    font-weight: normal;
    font-style: normal; }

.container {
	max-width: 1200px;
	width: 90%;
	margin: 0 auto;
	@include r(425) {
		width: 100vw; } }

