.product {
	&__wrapper {
		max-width: 960px;
		margin: 0 auto;
		max-width: 960px;
		position: relative;
		padding-top: 77px;
		@include r(1105) {
			max-width: 750px; }
		@include r(768) {
			padding-top: 40px; } }
	&__wrap {}
	&__title {
		text-align: center;
		font-family: Akrobatbold;
		font-size: 36px;
		text-transform: uppercase;
		margin-bottom: 67px;
		&_blue {
			font-family: Akrobat;
			color: #4890bd; }
		&_extra {
			font-family: Akrobat; }
		@include r(530) {
			font-size: 20px;
			margin-bottom: 20px; } }
	&__sale {
		color: red;
		font-family: KobzarKS;
		color: #d24a43;
		position: absolute;
		top: 0;
		right: 0;
		width: 248px;
		height: 145px;
		@include r(1105) {
			display: none; } }
	&__sale-size {
		position: absolute;
		font-size: 110px;
		line-height: 1;
		letter-spacing: 5.42px;
		top: 0;
		left: 0; }
	&__sale-caption {
		position: absolute;
		color: #d24a43;
		font-size: 17px;
		transform: rotate(-15deg);
		letter-spacing: 0.89px;
		right: 0;
		bottom: 0; }
	&__list {
		margin-bottom: 150px;
		display: inline-block;
		@include r(1105) {
			margin-bottom: 70px;
			zoom: 0.7;
			position: relative;
			left: 50%;
			transform: translate(-50%, 0); }
		@include r(670) {
			zoom: 0.6; }
		@include r(570) {
			zoom: 0.4; }
		@include r(500) {
			zoom: 0.35; }
		@include r(465) {
			zoom: .45; } }

	&__image {
		width: 337px;
		float: left;
		position: relative;
		@include r(100) {
			margin: 0 auto 20px;
			margin: 0;
			position: static;
			width: 50%;
			float: left;
			height: 200px; }
		img {
			max-width: 100%;
			height: 100%; } }
	&__quality {
		position: absolute;
		right: -11px;
		bottom: -11px;
		z-index: 9;
		@include r(780) {
			display: none;
			bottom: 0;
			right: 0; } }
	&__sale-image {
		width: 76px;
		height: 76px;
		position: absolute;
		padding: 17px 7px;
		display: inline-block;
		text-align: center;
		left: -38px;
		top: -38px;
		border: 2px solid #7b7b7b;
		background-color: rgba(249, 248, 255, 0.8);
		@include r(780) {
			display: none; }
		span {
			display: block;
			color: #d24a43;
			font-family: Akrobatbold;
			/*semi*/
			font-size: 27px;
			text-transform: uppercase;
			letter-spacing: 2.07px; } }
	&__stock {
		padding: 0 0 0 34px;
		width: 330px;
		float: left;
		margin-left: -34px;
		margin-top: -36px;
		background-color: #7F7F7F;
		text-align: center;
		@include r(101) {
			float: right;
			margin: 0 0 20px 0;
			padding: 0; }
		@include r(100) {
			float: right;
			width: 50%;
			margin: 0 auto;
			height: 200px; } }
	&__remainder {
		margin-top: 60px;
		width: 316px;
		float: left;
		@include r(425) {
			float: none;
			position: absolute;
			right: 0;
			bottom: 40px; } }
	&__caption {
		position: absolute;
		bottom: 0;
		left: 400px;
		@include r(425) {
			bottom: -70px;
			left: 0;
			right: 0;
			text-align: center; } } }

.product-stock {
	padding-top: 28px;
	&__title {
		padding-right: 34px;
		color: #ffffff;
		font-family: Akrobatbold;
		background-color: #808080;
		/*SemiBold*/
		font-size: 48px;
		text-transform: uppercase;
		letter-spacing: 2.92px;
		padding-bottom: 6px;
		position: relative;
		@include r(101) {
			padding: 0; }
		@include r(100) {
			padding-top: 5px;
			font-size: 24px;
			margin-bottom: 5px; }
		&:after {
			display: block;
			content: '';
			position: absolute;
			height: 2px;
			width: 114px;
			background-color: #3eb5f1;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 0); } }
	&__content {
		background-color: #767676;
		padding-right: 34px;
		@include r(101) {
			padding: 0; }
		@include r(100) {
			font-size: 18px; } }
	&__text {
		color: #e7e0d6;
		font-family: Akrobat;
		font-size: 18px;
		text-transform: uppercase;
		letter-spacing: 1.09px;
		line-height: 2em;
		@include r(101) {
			font-size: 12px; }
		&_bg {
			background-color: #808080;
			margin-right: -34px;
			@include r(100) {
				margin: 0; } } }
	&__old-price {
		text-shadow: 3px 4px 5px rgba(0, 0, 0, 0.15);
		color: #fd7979;
		font-family: Akrobat;
		font-size: 48px;
		font-weight: bold;
		text-transform: uppercase;
		letter-spacing: 2.92px;
		margin-bottom: 23px;
		@include r(101) {
			font-size: 24px;
			margin-bottom: 15px; } }
	&__button {
		box-shadow: 2px 3px 9px rgba(0, 0, 0, 0.25);
		border: 1px solid rgba(0, 0, 0, 0.35);
		background-color: #ffd357;
		border-radius: 40px;
		padding: 0 3px;
		color: #2d2d2d;
		font-family: Akrobat;
		font-size: 12px;
		font-weight: 400;
		text-transform: uppercase;
		letter-spacing: 0.9px;
		margin-bottom: 20px; } }

.remainder {
	background-color: #ECEDED;
	@include r(425) {
		background-color: transparent; }
	&__wrapper {
		padding: 15px 7px;
		@include r(425) {
			padding: 0; } }
	&__content {
		border: 1px solid #53a0cc;
		padding:  20px;
		text-align: center;
		@include r(425) {
			display: none;
			background-color: transparent; } }
	&__text {
		color: #2d2d2d;
		font-family: Akrobat;
		font-size: 18px;
		font-weight: 400;
		letter-spacing: 1px;
		margin-bottom: 20px;
		span {
			color: #53a0cc; }
		@include r(100) {
			font-size: 16px; } }
	&__phone {
		color: #53a0cc;
		font-family: Akrobat;
		font-size: 24px;
		font-weight: 400;
		// Text style for "+7 (495) 7"
		letter-spacing: 1.2px;
		@include r(100) {
			font-size: 20px; } }
	&__image {
		background-color: #FFF;
		text-align: center;
		padding: 10px 0;
		@include r(425) {
			background-color: transparent;
			z-index: -1; } } }
.caption {
	&__text {
		color: #555555;
		font-family: Akrobatbold;
		font-size: 22px;
		&_bold {
			color: #53a0cc;
			text-transform: uppercase; }
		@include r(100) {
			font-size: 16px; } } }

.info {
	text-align: center;
	padding: 20px 30px;
	background-color: #53A0CC;
	margin-top: -100px;
	@include r(640) {
		background-color: #fff; }
	@include r(800) {
		margin-top: 1px;
		width: 100%;
		padding: 10px 10px; }
	&__wrapper {
		padding: 20px 0;
		border: 1px solid #f6e43c;
		position: relative;
		@include r(800) {
			border: none;
			padding: 0; }
		&:after {
			display: block;
			content: '';
			position: absolute;
			@include s(fir-tree);
			bottom: 0;
			left: 20px;
			@include r(1000) {
				display: none; } }
		&:before {
			display: block;
			content: '';
			position: absolute;
			right: 0;
			top: -61px;
			@include s(car);
			@include r(1000) {
				display: none; } } }
	&__title {
		color: #ffd357;
		font-family: Akrobat;
		font-size: 24px;
		text-transform: uppercase;
		text-align: center;
		margin-bottom: 18px;
		margin-top: 18px;
		@include r(640) {
			background-color: #53A0CC;
			margin-bottom: 0;
			border-radius: 5px 5px 0 0; } }
	&__table {
		background-color: #FFF;
		border: 1px solid transparent;
		text-align: center;
		max-width: 600px;
		margin: 0 auto;
		color: #1f1f1f;
		font-family: Akrobat;
		line-height: 60px;
		margin-bottom: 40px;
		@include r(800) {
			width: 90%; } }
	&__button-wrap {
		@include r(640) {
			background-color: red;
			background-color: #53A0CC;
			padding: 10px 0;
			border-radius: 0 0 5px 5px; } }
	&__button {
		padding: 5px 40px;
		border: 3px solid #f6e43c;
		border-radius: 20px;
		background-color: transparent;
		color: #fff;
		font-family: Akrobat;
		font-size: 24px;
		line-height: 34px;
		text-transform: uppercase;
		@include r(640) {
			font-size: 20px; } } }
.info-table {
	border: none;
	position: relative;
	@include r(640) {
		display: none; }
	&:after {
		position: absolute;
		display: block;
		content: '';
		width: 60px;
		height: 60px;
		background: #fff url('../img/rulet.png') 50% 50% no-repeat;
		top: 0;
		height: 64px;
		left: -59px;
		box-shadow: 0 3px 3px rgba(0, 0, 0, 0.25);
		border: 1px solid rgba(98, 98, 98, 0.5);
		border-bottom: none;
		border-right: none;
		@include r(1000) {
			display: none; } }
	&:before {
		position: absolute;
		margin-top: -1px;
		top: 50%;
		display: block;
		content: '';
		width: 600px;
		height: 1px;
		background: url('../img/bg-table-2.png') 0 50% no-repeat;
		-webkit-background-size: 100% 100%;
		-o-background-size: 100% 100%;
		background-size: 100% 100%;
		@include r(800) {
			display: none; } }
	td {
		@include r(800) {
			border: 1px solid #000; } }
	&__head {
		font-size: 30px; }
	&__row {
		font-size: 20px;
		@include r(800) {
			border: 1px solid red; } }
	&__row_first {
		box-shadow: 0 3px 3px rgba(0, 0, 0, 0.25);
		border-top: 1px solid rgba(98, 98, 98, 0.5);
		&:after {
			content: '';
			background-color: red;
			width: 60px;
			height: 80px;
			position: absolute;
			bottom: 0;
			background: #626262 url('../img/ruble.png') 50% 50% no-repeat;
			border: 1px solid rgba(232, 232, 232, 1);
			border-left: none;
			@include r(1000) {
				display: none; } }
		:before {
			position: absolute;
			display: block;
			position: absolute;
			display: block;
			content: '';
			height: 120px;
			width: 1px;
			background: url('../img/bg-table.png') 50% 0 no-repeat;
			background-size: cover;
			top: 0;
			right: 0;
			@include r(800) {
				display: none; } } }
	&__row_grow {
		background-color: #626262;
		border: 1px solid rgba(232, 232, 232, 0.5);
		border-top: none;
		td {
			color: #fff;
			position: relative;
			&:before {
				position: absolute;
				display: block;
				content: '';
				height: 200%;
				width: 1px;
				background: url('../img/bg-table.png') 50% 0 no-repeat;
				background-size: cover;
				top: -100%;
				@include r(800) {
					display: none; } } }
		.hov:hover,	.hov-active {
			background-color: #ffd357;
			cursor: url('../img/cursor.png'), pointer;
			color: #000;
			@include r(800) {
				cursor: none;
				background-color: transparent;
				color: #fff; }
			&:after {
				display: block;
				content: '';
				width: 100%;
				height: 20px;
				position: absolute;
				bottom: -20px;
				background-color: #ffd357;
				@include r(800) {
					display: none;
					background-color: transparent; } } } } }

.info__tablle-small {
	display: none;
	width: 100%;
	margin: 0 auto;
	padding: 0 20px;
	background-color: #FFF;
	position: relative;
	@include r(640) {
		display: table;
		border-collapse: separate; }
	tr {
		width: 50%;
		position: relative;
		border-bottom: 1px solid #53A0CC; }
	td {
		height: 24px;
		vertical-align: middle;
		font-size: 14px; }
	.info__tablle-small_price {
		border-bottom: 1px solid #000;
		background-color: #FFF; } }
