  font-family: Akrobat;
  font-size: 26px;
  font-weight: 400;
  text-decoration: line-through;
  text-transform: uppercase;
  letter-spacing: 1.71px;
  margin-bottom: 20px;
}

.stock-sw__price {
  text-shadow: 3px 4px 5px rgba(0, 0, 0, 0.15);
  color: #fd7979;
  font-family: Akrobat;
  font-size: 37px;
  text-transform: uppercase;
  letter-spacing: 2.38px;
  margin-bottom: 39px;
}

.stock-sw__sale {
  color: #ffffff;
  font-family: Akrobat;
  font-size: 20px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.86px;
  margin-bottom: 40px;
}

.stock-sw__button {
  background-color: transparent;
  width: 170px;
  border: 2px solid #ffd357;
  border-radius: 20px;
  font-family: Akrobatbold;
  font-size: 19px;
  text-transform: uppercase;
  letter-spacing: 1.17px;
  padding: 14px 0;
}

.small-woods__text {
  padding-left: 80px;
}

@media only screen and (max-width: 700px) {
  .small-woods__text {
    padding: 0;
    text-align: center;
    margin-bottom: 10px;
  }
}

.nursery {
  padding: 80px 0;
}

.nursery__title {
  text-align: center;
  color: #1f1f1f;
  font-family: Akrobat;
  font-size: 45px;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 66px;
}

.nursery .grid {
  margin: 0 auto;
}

.nursery__image-big:after {
  display: block;
  position: absolute;
  content: '';
  border: 1px solid #fff;
  left: 5px;
  right: 5px;
  top: 10px;
  bottom: 15px;
}

.nursery__caption {
  padding-top: 18px;
  height: 76px;
  background-color: rgba(255, 255, 255, 0.4);
  position: absolute;
  left: 6px;
  right: 6px;
  bottom: 16px;
  color: #fff;
  font-family: Akrobat;
  font-size: 25px;
  text-align: center;
  text-transform: uppercase;
}

.nursery__caption:after {
  display: inline-block;
  content: '';
  height: 2px;
  background-color: #3eb5f1;
  position: absolute;
  left: 14px;
  right: 14px;
  bottom: 16px;
}

.grid-item {
  width: 285px;
  margin-bottom: 20px;
}

.grid-item--width2 {
  width: 588px;
}

.comands {
  padding: 80px 0;
}

@media only screen and (max-width: 530px) {
  .comands {
    display: none;
  }
}

.comands__title {
  text-align: center;
  color: #1f1f1f;
  font-family: Akrobat;
  font-size: 45px;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 52px;
}

.comands__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.comands__item {
  text-align: center;
  margin: 0 7px 20px;
  padding: 17px 0 47px;
  background-color: #e8e8e8;
  text-align: center;
}

.comands__image {
  margin: 0 47px 35px 47px;
  border-radius: 19px;
  width: 141px;
  height: 141px;
  overflow: hidden;
  background-color: transparent;
}

.comands__caption {
  padding: 17px 0;
  background-color: #747474;
}

.comands__name {
  color: #ffffff;
  font-family: Akrobat;
  font-size: 20px;
  text-transform: uppercase;
}

.comands__name:after {
  display: block;
  content: '';
  width: 130px;
  height: 2px;
  background-color: #3eb5f1;
  margin: 15px auto 19px;
}

.comands__position {
  color: #ffffff;
  font-family: Akrobat;
  font-size: 16px;
  font-weight: 700;
}

.map {
  padding-top: 80px;
  margin: 0 auto;
  text-align: center;
  max-width: 100%;
  max-height: 600px;
  height: 80vh;
}

@media only screen and (max-width: 930px) {
  .map {
    padding: 60px 0;
  }
}

@media only screen and (max-width: 700px) {
  .map {
    padding: 40px 0;
  }
}

.footer {
  background-color: #525252;
  padding: 85px 0 67px;
}

@media only screen and (max-width: 510px) {
  .footer {
    padding: 10px;
  }
}

.footer__wrap {
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
  display: flex;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  justify-content: space-between;
  color: #dcdcdc;
  font-family: Akrobat;
  font-size: 16px;
}

@media only screen and (max-width: 720px) {
  .footer__wrap {
    -webkit-justify-content: center;
    justify-content: center;
  }
}

.footer__title {
  text-transform: uppercase;
}

.footer__title:after {
  display: block;
  content: '';
  width: 106px;
  height: 2px;
  background-color: #6ac0f3;
  margin-top: 24px;
  margin-bottom: 27px;
}

.footer__item {
  margin: 0 20px 20px 0;
}

@media only screen and (max-width: 720px) {
  .footer__item {
    margin-right: 40px;
  }
}

.footer__item::last-child {
  margin-right: 0;
}

@media only screen and (max-width: 510px) {
  .footer__item {
    display: none;
  }
}

.footer__list li {
  margin-bottom: 16px;
}

.footer__list a {
  color: #dcdcdc;
  font-family: Akrobat;
  font-size: 16px;
  text-decoration: none;
}

.footer__contacts {
  text-align: center;
  border: 2px solid #6ac0f3;
  padding: 25px 25px 38px;
  font-size: 16px;
}

@media only screen and (max-width: 510px) {
  .footer__contacts {
    width: 100%;
    position: relative;
    padding: 25px 25px 38px;
  }
}

.footer__phone {
  color: #dcdcdc;
  font-family: Akrobatbold;
  margin-bottom: 35px;
}

@media only screen and (max-width: 510px) {
  .footer__phone {
    display: inline-block;
    margin-right: 20px;
  }
}

.footer__hour-work {
  margin-bottom: 35px;
  position: absolute;
  text-align: center;
  left: 0;
  right: 0;
  top: 55px;
}

.footer__email {
  margin-bottom: 30px;
}

@media only screen and (max-width: 510px) {
  .footer__email {
    display: inline-block;
  }
}
