.features {
	padding: 80px 0;
	@include r(820) {
		padding: 65px 0; }
	@include r(700) {
		padding: 55px 0; }
	@include r(400) {
		padding: 40px 0; }
	@include r(375) {
		display: none; }
	&__list {
		max-width: 981px;
		margin: 0 auto;
		display: flex;
		-webkit-justify-content: space-between;
		justify-content: space-between;
		align-items: baseline;
		align-content: center; }
	&__item {
		text-align: center;
		p {
			color: #000000;
			font-family: Akrobat;
			font-size: 24px;
			font-weight: 300;
			text-transform: uppercase;
			text-align: center;
			padding: 0 5px;
			@include r(820) {
				font-size: 20px; }
			@include r(700) {
				font-size: 16px; }
			@include r(400) {
				font-size: 12px; } }
		.icon {
			font-size: 60px;
			margin-bottom: 20px;
			@include r(820) {
				font-size: 50px; }
			@include r(700) {
				font-size: 30px; }
			@include r(400) {
				font-size: 20px; } }
		span {
			display: inline-block; }
		.icon {
			fill: #ddd; } } }
