.elite {
	padding: 80px 0;
	@include r(600) {
		padding: 60px 0; }
	@include r(400) {
		padding: 40px 0; }
	max-width: 1900px;
	width: 90%;
	margin: 0 auto;
	&__heading {
		color: #1f1f1f;
		font-family: Akrobat;
		font-size: 50px;
		text-transform: uppercase;
		text-align: center;
		margin-bottom: 20px;
		@include r(600) {
			font-size: 40px;
			margin-bottom: 25px; }
		@include r(400) {
			font-size: 20px;
			margin-bottom: 10px; } }
	&__list {
		width: 100%;
		margin: 0 auto;
		display: flex;
		-webkit-justify-content: space-between;
		-webkit-justify-content: space-between;
		justify-content: center;
		@include r(1100) {
			-webkit-flex-wrap: wrap;
			-ms-flex-wrap: wrap;
			flex-wrap: wrap; } }
	&__image {
		width: 100%;
		position: relative;
		margin-bottom: 20px;
		overflow: hidden;
		@include r(1200) {
			margin-bottom: 20px; }
		@include r(1000) {
			margin-bottom: 20px; }
		img {
			max-width: 100%; } }
	&__item {
		position: relative;
		margin: 0 5px;
		width: 32%;
		@include r(832) {
			margin-bottom: 20px;
			width: 47%; }
		@include r(360) {
			width: 40%; } }
	&__caption {
		padding: 20px 20px 13px 60px;
		position: absolute;
		background-color: rgba(255,255,255, .2);
		top: 40px;
		left: 0;
		right: 0;
		color: #eee;
		@include r(1200) {
			padding: 0;
			position: static;
			color: #000; } }
	&__title {
		color: #eee;
		font-family: Akrobat;
		font-size: 26px;
		text-transform: uppercase;
		max-width: 252px;
		border-bottom: 2px solid #6ac0f3;
		padding-bottom: 17px;
		margin-bottom: 15px;
		@include r(1600) {
			font-size: 20px;
			border-bottom: none; }
		@include r(1200) {
			color: #000;
			padding-bottom: 5px;
			margin-bottom: 5px; }
		@include r(400) {
			font-size: 18px; }
		@include r(340) {
			font-size: 16px; } }
	&__text {
		color: #eee;
		font-family: Akrobat;
		font-size: 18px;
		letter-spacing: 1.14px;
		@include r(1600) {
			font-size: 16px; }
		@include r(1200) {
			font-size: 12px;
			color: #000; } }
	&__price {
		text-align: center;
		color: #000000;
		font-family: Akrobat;
		font-size: 30px;
		text-transform: uppercase;
		// Text style for ",   ,  "
		font-weight: 300;
		// Text style for "652 000"
		@include r(1600) {
			font-size: 20px; }
		@include r(1200) {
			text-align: left;
			font-size: 12px;
			text-transform: capitalize; } }
	&__size-price {
		color: #d24a43;
		font-family: Akrobatbold;
		font-size: 36px;
		font-weight: 700;
		@include r(1600) {
			font-size: 25px; }
		@include r(1200) {
			font-size: 18px; } } }
.dlider-elite__dots {
	position: absolute;
	bottom: -40px;
	li {
		display: inline-block;
		font-size: 0; }
	li+li {
		margin-left: 10px; }
	button {
		font-size: 0;
		display: block;
		width: 15px;
		height: 15px;
		background-color: #fff;
		border: 2px solid #000;
		border-radius: 50%; } }


