.goods {
	zoom: 0.9;
	@include r(1400) {
		zoom: .55; }
	&__title {
		text-align: center;
		color: #1f1f1f;
		font-family: Akrobat;
		font-size: 60px;
		text-transform: uppercase;
		color: #010101;
		margin-bottom: 58px;
		@include r(375) {
			font-size: 40px;
			margin-bottom: 20px; }
		span {
			color: #4890bd; } }
	&__sale {
		position: absolute;
		font-size: 20px;
		color: #FFF;
		top: 4px;
		left: -52px;
		width: 250px;
		box-shadow: 2px 3px 7px rgba(0, 0, 0, 0.35);
		background-color: #ffd357;
		transform: rotate(-30deg);
		padding: 10px 0;
		text-align: center;
		color: #3b3b3b;
		font-family: "Lucida Sans Unicode";
		font-size: 37px;
		font-weight: 400;
		text-transform: uppercase; }
	&__images-wrapper {
		@include clr;
		font-size: 0; }
	&__image {
		position: relative;
		width: 539px;
		overflow: hidden;
		margin-bottom: 20px;
		@include r(700) {
			display: block;
			margin: 0 auto 20px; }
		@include r(375) {
			width: 60%;
			height: 60%; }
		&:after {
			display: block;
			content: '';
			position: absolute;
			top: 7px;
			bottom: 7px;
			right: 7px;
			left: 7px;
			border: 1px solid #fff; }
		img {
			transition: all 1s ease-out;
			max-width: 100%; }
		&_big {
			margin-left: 25px;
			float: left;
			@include r(700) {
				float: none; } }
		&_small {
			float: right;
			margin-right: 25px;
			margin-bottom: 35px;
			@include r(700) {
				float: none; }
			@include r(530) {
				display: none; } } }
	&__description {
		@include clr;
		@include r(700) {
			text-align: center; } }
	&__table {
		float: left;
		width: 539px;
		margin-left: 25px;
		margin-bottom: 40px;
		@include r(700) {
			float: none;
			margin: 0 auto 40px; }
		&-head {
			font-family: Akrobatbold;
			background-color: #dcdcdc;
			text-transform: uppercase;
			/*semi bold*/ }
		td {
			padding: 17px 0;
			border: 1px solid #000000;
			text-align: center;
			color: #3b3b3b;
			font-family: Akrobat;
			font-size: 26px;
			background-color: #fff; }
		td.active {
			background-color: #ffd357;
			span {
				color: #d51a1a; } } }
	&__button {
		float: right;
		margin: 23px 25px 0 0;
		width: 253px;
		border: 2px solid #4890bd;
		text-align: center;
		border-radius: 28px;
		background-color: transparent;
		color: #3b3b3b;
		font-family: Akrobat;
		/*semi bold*/
		padding: 22px 0;
		font-size: 28px;
		text-transform: uppercase;
		@include r(700) {
			float: none;
			margin: 0;
			display: inline-block; } } }




